50% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes text-two {
0% {
    opacity: 0;
}
50% {
    opacity: 0.5;
}
100% {
    opacity: 1;
}
}
.home-post .start {
  position: fixed;
  left: 50%;
  margin-left: -17px;
  top: 95%;
  margin-top: -34px;
  width: 34px;
  height: 34px;
}
.home-post .start b {
  position: absolute;
  left: 50%;
  top: 10px;
  margin-left: -9px;
  width: 19px;
  height: 12px;
  background: url(data:image/png;base64,);
  background-size: 100% 100%;
  -webkit-animation: start 1.5s infinite ease-in-out;
  animation: start 1.5s infinite ease-in-out;
}
.home-post .fullPage-wp {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.home-post .fullPage-wp:after {
  display: block;
  content: ' ';
  height: 0;
  clear: both;
}
.home-post .fullPage-wp.anim {
  -webkit-transition: all 500ms ease-out 0s;
  transition: all 500ms ease-out 0s;
}
.home-post .fullPage-page {
  display: block;
  overflow: hidden;
}
.home-post .fullPage-dir-h {
  float: left;
}
@keyframes tada {
0% {
    transform: scale(0.9);
    -webkit-transform: scale(0.9);
}
10%,
  20% {
    transform: scale(1);
    -webkit-transform: scale(1);
}
30%,
  50%,
  70%,
  90% {
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
}
40%,
  60%,
  80% {
    transform: scale(1.05);
    -webkit-transform: scale(1.05);
}
100% {
    transform: scale(0.95);
    -webkit-transform: scale(0.95);
}
}
